module.exports = ({ stores, styles }) => () => {

    const $styles = document.createElement('div');
    $styles.className = 'styles';

    const appendRoleStyle = roleId => $styles.append(styles.role(roleId));
    const appendTagStyle = tagId => $styles.append(styles.tag(tagId));

    stores.roles.ids().forEach(appendRoleStyle);
    stores.roles.onInsert(appendRoleStyle);

    stores.tags.ids().forEach(appendTagStyle);
    stores.tags.onInsert(appendTagStyle);

    $styles.append(
        styles.colourPicker(),
        styles.main(),
        styles.tags()
    );

    return $styles;

};
