module.exports = ({ stores, components }) => instanceId => {

    const $name = components.editableSpan(e => {
        const name = e.target.textContent.trim();
        const { tagId } = stores.tagInstances.getState(instanceId);
        stores.tags.setState(tagId, { name });
    });

    $name.className = 'tag-name';

    stores.tagInstances.onChange(instanceId, {
        name: () => {
            const { name } = stores.tagInstances.getState(instanceId);
            $name.textContent = name;
        }
    });

    return $name;

};
