module.exports = ({ lib, stores }) => () => {

    const $style = document.createElement('style');

    stores.options.onChange(lib.sameValue(['shape', 'size', 'spacing'], () => {
        const { shape, size, spacing } = stores.options.getState();
        const borderRadius = shape === 'circle' ? 50 : 0;

        // TODO: refactor width calc.

        $style.textContent = `
            .tag {
                margin: ${spacing}px;
                width: ${size + 5 + 5 + 10 + 10}px;
            }

            .tag-image-container {
                height: ${size}px;
                width: ${size}px;
            }

            .tag-image {
                border-radius: ${borderRadius}%;
            }`;
    }));

    return $style;

};
