module.exports = ({ stores, services, components }) => instanceId => {

    const $role = components.editableSpan(e => {
        const name = e.target.textContent.trim();
        const roleId = services.roles.findOrInsertRoleWithName(name);
        const { tagId } = stores.tagInstances.getState(instanceId);
        stores.tags.setState(tagId, { roleId });
    });

    $role.className = 'role-badge';

    stores.tagInstances.onChange(instanceId, {
        roleName: () => {
            const { roleName } = stores.tagInstances.getState(instanceId);
            $role.textContent = roleName;
        }
    });

    return $role;

};
