module.exports = ({ stores }) => fallback => {

    const $fallback = document.createElement('img');
    $fallback.className = 'fallback';
    $fallback.title = fallback;
    $fallback.src = `img/gravatar-fallbacks/${fallback}.png`;

    $fallback.addEventListener('click', () => {
        stores.gravatar.setState({ fallback });
    });

    stores.gravatar.onChange({
        fallback: () => {
            const { fallback: selectedFallback } = stores.gravatar.getState();
            $fallback.classList.toggle('selected', fallback === selectedFallback);
        }
    });

    return $fallback;

};