module.exports = ({ stores, views }) => instanceId => {

    let $layout;

    stores.options.onChange({
        layout: () => {
            const $newLayout = document.createElement('span');

            const { layout } = stores.options.getState();

            const rows = layout.map(row => {
                const $row = document.createElement('div');
                $row.className = 'row';
                const components = row.map(name => views.tags[name](instanceId));
                components.forEach($component => $row.append($component))
                return $row;
            });

            rows.forEach($row => $newLayout.append($row));
            $layout = $newLayout
            // $layout = $layout ? $layout.replaceWith($newLayout) : $newLayout; // not in use
        }
    });

    return $layout;

};
