module.exports = ({ stores, components }) => () => {

    const $freetext = document.createElement('textarea');
    $freetext.className = 'freetext';

    $freetext.addEventListener('blur', e => {
        const freetext = e.target.value;
        stores.gravatar.setState({ freetext });
    });

    stores.gravatar.onChange({
        freetext: () => {
            const { freetext } = stores.gravatar.getState();
            $freetext.value = freetext;
        },
        status: () => {
            const { status } = stores.gravatar.getState();
            $freetext.disabled = status === 'working';
        }
    });

    const labelText = `
        Enter email addresses to import images from Gravatar.<br />
        Alternatively, enter names to use one of the generated image styles below.`;

    return components.label(labelText, $freetext);

};
