module.exports = ({ stores }) => () => {

    const { keepSorted } = stores.options.getState();
    const { nilRoleId } = stores.app.getState();

    const roles = stores.roles.getItems().filter(role => role.id !== nilRoleId);
    const sortRoles = () => roles.sort((a, b) => a.name.localeCompare(b.name));
    const nilRole = stores.roles.getState(nilRoleId);
    const sortedRoles = keepSorted ? sortRoles() : roles;
    return sortedRoles.concat(nilRole);

};
