module.exports = ({ stores }) => () => {

    const $keepSorted = document.createElement('span');
    $keepSorted.className = 'keep-sorted';
    $keepSorted.innerText = 'Keep sorted';
    $keepSorted.title = 'Keep sorted by role and name';

    $keepSorted.addEventListener('click', () => {
        const { keepSorted } = stores.options.getState();
        stores.options.setState({ keepSorted: !keepSorted });
    });

    stores.options.onChange({
        keepSorted: () => {
            const { keepSorted } = stores.options.getState();
            $keepSorted.classList.toggle('selected', keepSorted);
        }
    });

    return $keepSorted;

};
